@import '../../common//colors.scss';
.card-item{
    cursor: pointer;
    background:$primary-color;
    border-radius: 15px;
    transition: all 0.3s;
    margin: 15px;
}
.card-item:hover{
    transform: scale(1.1);
    transition: all 0.3s;

}
.card-top{
    height: 300px;
    img{
        width: 100%;
        height: 100%;
    }
}

.card-bottom{
    padding: 10px;

}
.card-info{
    h4{

        font-size: 22px;
        font-weight: 400;
        margin-bottom: 10px;
    }
}
