@import '../../common//colors.scss';

.movie-list{
    margin: 10px 30px;
    h2{
        margin-bottom: 10px;
        font-weight: 400;
    }
}


.cat-name{
    text-align:center;
    color: $heading-color;
    font-size:10vh;
}
@media only screen and (max-width: 600px) {
    .cat-name{
        font-size:4vh;
    }
    .movie-wrapper{
        display: flex;
        flex-direction: column;
        .head {
            padding-top: 60px;
            margin-bottom: 0px;
            padding-bottom: 0px;
        }
        .bottom {
            margin-top: 0px;
            padding-top: 0px;
            margin-bottom: 0px;
        }
    }
    
}