@import '../../common//colors.scss';

.navbar{
    background:$primary-color ;

    display: flex;
    justify-content: space-between;
    align-items: center;
   

}
.logo img{
height: 2.5rem;
padding: 5px;
}
.logo {
    display: flex;
    justify-content: center;
    align-items: center;


}
.user{
    display: flex;
    padding: 10px;
    justify-content: right;
}
.user img{
    height: 2rem;
    padding: 5px;


}

.logotitle{
    h1{
        margin: 0;
        color: $heading-color;
        margin-bottom: 0;
    }
    small{
        margin-top: -5px;

        text-align: center;
        font-size:x-small;
    }
    color: white;
    padding: 5px;
    display: flex;
    flex-direction: column;
    margin: 0;
}
.search-bar{
    display: flex;
    justify-content: center;
    align-items: center;
    input{
        padding: 10px ;
        padding-left: 15px;
        border: 0;
        border-radius: 20px 0px 0px 20px;
        outline: none;
    }
    button{
        display: flex;
        justify-content: center;
        background: white;
        padding: 5px;
        border: 0;
        outline: none;
        cursor: pointer;
        border-radius: 0px 20px 20px 0px;

    }
   
}
.right-bar{
    display: flex;
    justify-content: center;
    align-items: center;
}
@media only screen and (max-width: 600px) {
    .search-bar{
       
        input{
            padding: 10px ;
            padding-left: 15px;
            border: 0;
            border-radius: 20px 0px 0px 20px;
            outline: none;
            width: 5rem;

        }
        
       
    }
    .logo img{
        height: 2rem;
        padding: 5px;
        }
        .logotitle{
            h1{
                margin: 0;
                color: $heading-color;
                margin-bottom: 0;
                font-size: x-large;
            }
            small{
                margin-top: -5px;
        
                text-align: center;
                font-size:xx-small;
            }
            color: white;
            padding: 5px;
            display: flex;
            flex-direction: column;
            margin: 0;
        }
        .navbar{
            position: sticky;
            position: fixed;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            z-index: 2000;
        
        }
       .right-bar{
        padding-left: 20px;
       }
       
       
}