@import '../../common//colors.scss';

.movie-section{
    display: flex;
    flex-direction: row;
    padding: 30px 30px;
    justify-content: space-evenly;

}
.movie-title,.loading{
    display: flex;
    justify-content: center;
    align-items: center
    ;
    color: $heading-color;
    font-size: 7vh;
}
.movie-rating{
    margin-top: 20px;
    display: flex;
    justify-content:space-evenly;
}
.movie-plot{
    margin-top: 20px;
    line-height: 1.8rem;
}
.movie-info> div span:first-child{
    padding: 10px 0px;
    font-weight: 600;
    width: 100px;
    display: inline-block;
}

@media only screen and (max-width: 600px) {
   
    .movie-section{
        display: flex;
        flex-direction: column-reverse;
        padding: 15px 15px;
        justify-content: center;
        align-items: center;
        padding-top: 50px;

        .movie-title{
            text-align: center;
            font-size:xx-large;
        }
      
    
    }
    .movie-info{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .loading{
       margin-top: 30vh;
       font-size: xx-large;
    }
  }